// Dashboard.js
import React from 'react';
// import Cart from './cart'
import Adash from './admindash'

import { useHistory, useNavigate } from 'react-router-dom';
const Dashboard = ({ onLogout, userName }) => {
  const navigate = useNavigate();
  const handleLogout = () => {
    // Perform logout logic (e.g., clear user session, redirect to login)
    onLogout();
    navigate('/');
  };
  return (
    <div>
    <h2 style={{color:"black"}}>Welcome to the Dashboard, {userName}!</h2>
<div > {userName && <Adash userName={userName} />}</div>
    <button style={{marginLeft:"5%"}} onClick={handleLogout}>Logout</button>
  </div>
  );
};

export default Dashboard;
