import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import PublicIcon from '@mui/icons-material/Public';
import { IconButton } from '@mui/material';
import '../components/signup.css'

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));
  


const RegisterForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [mobile, setMobile] = useState('');
  const [message, setMessage] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
        const response = await fetch('http://localhost:3000/register', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ name, email, password,  mobile }),
  
        });
  
        if (response.ok) {
          console.log('Registration successful');
          navigate('/');
        } else {
          const errorData = await response.json();
          console.log('Registration failed:', errorData.message);
          // Handle registration failure (e.g., display error message)
        }
      } catch (error) {
        console.error('Error occurred during registration:', error);
        // Handle registration error (e.g., display error message)
      }
    };

  return (
    <div>
         <Box sx={{ flexGrow: 1 }}>
        <Grid container >
          <Grid item xs={12} md={12} lg={6} className='backcolor'>
            <div>
              <p className='title'><img src='https://sajaeventsandhospitality.com/img2/logo30.png' style={{ width: "60%" }} /></p>
            </div>
            <div>
         
                <div>
                  <h3>REGISTRATION</h3>
                  <form onSubmit={handleSubmit} style={{}}>
                    <div className='input-box' style={{ margin: "auto" }}>

                      <input
                        type="text"

                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        required
                      />
                      <label>Name</label>
                    </div>

                    <div className='input-box' style={{ margin: "auto", marginTop: "3%" }}>

                      <input
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                      />
                      <label>Email</label>

                    </div>
                    <div className='input-box' style={{ margin: "auto", marginTop: "3%" }}>

                      <input
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                      />
                      <label>Password</label>

                    </div>
                    <div className='input-box' style={{ margin: "auto", marginTop: "3%" }}>

                      <input
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                      />
                      <label> Confirm Password</label>

                    </div>
                 

                    <div className='input-box' style={{ margin: "auto", marginTop: "3%" }}>
                      <input

                        type="number"
                        pattern="[0-9]*"
                        inputMode="numeric"  // Sets the keyboard input mode to numeric
                        value={mobile}
                        onChange={(e) => setMobile(e.target.value)}
                        required
                      />
                      <label>Mobile Number</label>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center',alignItems:"center" }}>
                   
                      <button type="submit" className="register-button" style={{marginLeft:"-5%"}}>Register</button>
                      
                    </div>
                  </form>
                </div>
         
            </div>
            <div style={{ display: "flex",marginTop:"5%"  }}>
           <IconButton style={{color:"#919191 " ,fontSize:"14px",marginLeft:"15%"}}>
            <PublicIcon style={{color:"#919191 "}} /> Saja Events
           </IconButton>
           <IconButton style={{color:"#919191 " ,fontSize:"14px",marginLeft:"1%"}}>
            <FacebookIcon style={{color:"#919191 "}} /> Facebook
           </IconButton>
           <IconButton style={{color:"#919191 " ,fontSize:"14px",marginLeft:"2%"}}>
            <InstagramIcon style={{color:"#919191 "}} /> Instagram
           </IconButton>
           <IconButton style={{color:"#919191 " ,fontSize:"14px",marginLeft:"3%"}}>
            <LinkedInIcon style={{color:"#919191 "}} /> LinkedIn
           </IconButton>
            </div>
          </Grid>
          <Grid item md={6} lg={6} className='img'>

            <img src='https://solusta.onsinch.com/agencies/themeMedia/288' style={{ width: "100%", height: "694px" }} />
          </Grid>
        </Grid>
      </Box>
    
    </div>
  );
};

export default RegisterForm;
