import * as React from 'react';
import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import WorkIcon from '@mui/icons-material/Work';
import FeedbackIcon from '@mui/icons-material/Feedback';
import AssignmentIndIcon from '@mui/icons-material/AssignmentInd';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import BorderColorIcon from '@mui/icons-material/BorderColor';
import InfoIcon from '@mui/icons-material/Info';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import AccountCircle from '@mui/icons-material/AccountCircle';
import NotificationsIcon from '@mui/icons-material/Notifications';
import Badge from '@mui/material/Badge';
import Modal from '@mui/material/Modal';
import { Link } from 'react-router-dom';
import  { useState } from 'react';
import OutlinedInput from '@mui/material/OutlinedInput';
import Checkbox from '@mui/material/Checkbox';
import Switch from '@mui/material/Switch';
import SearchIcon from '@mui/icons-material/Search';
import EventIcon from '@mui/icons-material/Event';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Tabs from '@mui/material/Tabs';
import RestoreIcon from '@mui/icons-material/Restore';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import MoreIcon from '@mui/icons-material/MoreVert';


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));







const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};
const label = { inputProps: { 'aria-label': 'Switch demo' } };
const names = [
  'Oliver Hansen',
  'Van Henry',
  'April Tucker',
  'Ralph Hubbard',
  'Omar Alexander',
  'Carlos Abbott',
  'Miriam Wagner',
  'Bradley Wilkerson',
  'Virginia Andrews',
  'Kelly Snyder',
];

const drawerWidth = 240;
const bull = (
  <Box
    component="span"
    sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
  >
    
  </Box>
);
const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: 'hidden',
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: 'hidden',
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up('sm')]: {
    width: `calc(${theme.spacing(8)} + 1px)`,
  },
});

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    boxSizing: 'border-box',
    ...(open && {
      ...openedMixin(theme),
      '& .MuiDrawer-paper': openedMixin(theme),
    }),
    ...(!open && {
      ...closedMixin(theme),
      '& .MuiDrawer-paper': closedMixin(theme),
    }),
  }),
);










export default function MiniDrawer() {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };
  const [value, setValue] = React.useState('1');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };



 
    const [selectedDate, setSelectedDate] = useState('');
  
    const handleDateChange = (event) => {
      setSelectedDate(event.target.value);
    };



    const [personName, setPersonName] = React.useState([]);

    const handlePlaceChange = (event) => {
      const {
        target: { value },
      } = event;
      setPersonName(
        // On autofill we get a stringified value.
        typeof value === 'string' ? value.split(',') : value,
      );
    };


  return (
    <div>
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar position="fixed" open={open} style={{backgroundColor:"black"}}>
      <Toolbar>
          <IconButton
         color="inherit"
         aria-label="open drawer"
         onClick={handleDrawerOpen}
         edge="start"
         sx={{
           ...(open && { display: 'none' }),
         }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ display: { xs: 'none', sm: 'block' } }}
          >
            MUI
          </Typography>
     
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
            <IconButton size="large" aria-label="show 4 new mails" color="inherit">
              <Badge badgeContent={4} color="error">
                <MailIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              aria-label="show 17 new notifications"
              color="inherit"
            >
              <Badge badgeContent={17} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              edge="end"
              aria-label="account of current user"
              aria-haspopup="true"
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
          </Box>
          <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="show more"
              aria-haspopup="true"
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>
      <Drawer variant="permanent" open={open}>
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
            {['My Agenda'].map((text, index) => (

              <ListItem key={text} disablePadding sx={{ display: 'block' }}>
                <ListItemButton
                  sx={{
                    minHeight: 48,
                    justifyContent: open ? 'initial' : 'center',
                    px: 2.5,
                  }}
                >
                  <ListItemIcon
                    sx={{
                      minWidth: 0,
                      mr: open ? 3 : 'auto',
                      justifyContent: 'center',
                    }}
                  >
                    <Link to="/Dashboard">
                      <WorkIcon />
                    </Link>

                  </ListItemIcon>
                  <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
                </ListItemButton>
              </ListItem>
            ))}
            {['History'].map((text, index) => (

              <ListItem key={text} disablePadding sx={{ display: 'block' }}>
                <ListItemButton
                  sx={{
                    minHeight: 48,
                    justifyContent: open ? 'initial' : 'center',
                    px: 2.5,
                  }}
                >
                  <ListItemIcon
                    sx={{
                      minWidth: 0,
                      mr: open ? 3 : 'auto',
                      justifyContent: 'center',
                    }}
                  >
                    <RestoreIcon />

                  </ListItemIcon>
                  <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
          {['Available Shifts'].map((text, index) => (

            <ListItem key={text} disablePadding sx={{ display: 'block' }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? 'initial' : 'center',
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : 'auto',
                    justifyContent: 'center',
                  }}
                >
                  <InboxIcon />

                </ListItemIcon>
                <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
              </ListItemButton>
            </ListItem>
          ))}

          {['Wallet'].map((text, index) => (

            <ListItem key={text} disablePadding sx={{ display: 'block' }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? 'initial' : 'center',
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : 'auto',
                    justifyContent: 'center',
                  }}
                >
                  <Link to="/wallet">
                    <AccountBalanceWalletIcon />
                  </Link>
                </ListItemIcon>
                <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
              </ListItemButton>
            </ListItem>
          ))}

          {['Calender'].map((text, index) => (

            <ListItem key={text} disablePadding sx={{ display: 'block' }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? 'initial' : 'center',
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : 'auto',
                    justifyContent: 'center',
                  }}
                >
                  <Link to="/calender">

                    <EventIcon />
                  </Link>
                </ListItemIcon>
                <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
              </ListItemButton>
            </ListItem>
          ))}
          {['Appointments'].map((text, index) => (

            <ListItem key={text} disablePadding sx={{ display: 'block' }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? 'initial' : 'center',
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : 'auto',
                    justifyContent: 'center',
                  }}
                >
                  <Link to="/appointment">
                    <AssignmentIndIcon />
                    </Link>
                </ListItemIcon>
                <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
              </ListItemButton>
            </ListItem>
          ))}
      </Drawer>
      <Box component="main" sx={{ flexGrow: 1, p: 3 , }}>

    

      <Box sx={{ flexGrow: 1 }}>
      <Grid container spacing={2}>
        <Grid item xs={12} md={12} lg={8}>
          <Item>
          <Card sx={{  display:"flex"}}>
          
          <Box style={{width:"8%",height:"500px",backgroundColor:"#1976d0"}}>
          <InfoIcon style={{marginTop:"350%",color:"white"}}/>
          </Box>
          <Box>

      <CardContent>
        <Typography sx={{ fontSize: "15px",fontWeight:600 }}  gutterBottom>
        Hello and welcome to your new profile with Solusta!<br/>
        <Typography style={{fontSize:"16px"}}>
        First of all, watch this introduction video to understand how to use your profile.
        </Typography>
        <Typography style={{fontSize:"16px"}}>
        Next, fill up your basic information and your profile attributes, if you haven't done so already.
        </Typography>
        <Typography style={{fontSize:"16px"}}>
        Watch this video to learn how the interview will be conducted and how you can prepare for it
        </Typography>
        <Typography style={{fontSize:"16px"}}>
        Read about the interview process 
Check the list of available interviews in the Appointments
        </Typography>
        <Typography style={{fontSize:"16px"}}>
        If you have any additional questions, please contact our administrator via Telegram
        </Typography>
        <Typography style={{fontSize:"16px"}}>
        First of all, watch this introduction video to understand how to use your profile.
        </Typography>
        </Typography>
        
      </CardContent>
      <CardActions>
     
      </CardActions>
      </Box>
    </Card>
          </Item>
        </Grid>
        <Grid item xs={12} md={12} lg={4}>
          <Item>
          <Card sx={{  display:"flex",height:"320px"}}>
          
          <Box style={{width:"8%",height:"350px",backgroundColor:"#cc6600"}}>
          <BorderColorIcon style={{marginTop:"500%",color:"white"}}/>
          </Box>
          <Box>

      <CardContent>
        <Typography sx={{ fontSize: "15px",fontWeight:600 }}  gutterBottom>
        Please fill in the missing information in your profile<br/>
        <Typography style={{fontSize:"16px"}}>
Some mandatory information is missing from your profile. Please complete your profile so that we can approve it and you can start working as soon as possible.
</Typography>
        </Typography>
        
      </CardContent>
      <CardActions>
        <Link to="/profile">
      <Button variant="contained" style={{ backgroundColor: 'black', width: 'max-content', color: 'white',border:"1px solid #FEBC1D" }}>
              Edit profile
            </Button>
            </Link>
      </CardActions>
      </Box>
    </Card>
          </Item>
        </Grid>
     
      </Grid>
    </Box>









       
     <Box sx={{ flexGrow: 1 , marginTop:"3%"}}>
      <Grid container spacing={2}>
        
        <Grid item xs={12} md={12} lg={4}>
          <Item>

          <TabContext value={value}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <TabList onChange={handleChange} aria-label="lab API tabs example">
            <Tab label="Item One" value="1" />
            <Tab label="Item Two" value="2" />
         
          </TabList>
        </Box>
        <TabPanel value="1">
      <Box sx={{ width:"100%",height:"500px" ,}}>
      <TextField id="outlined-basic" label="Keyword" variant="outlined" />
      <Box style={{display:"flex"}}>
      <TextField id="outlined-basic" label="Place" variant="outlined" style={{marginTop:"5%",width:"40%",marginLeft:"8%"}} />
         <Box sx={{ minWidth: 120 ,marginTop:"5%"}}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Radius</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
     
          label="Age"
        
        >
          <MenuItem value={10}>Ten</MenuItem>
          <MenuItem value={20}>Twenty</MenuItem>
          <MenuItem value={30}>Thirty</MenuItem>
        </Select>
      </FormControl>
    </Box>
    
      </Box>
    
      <Box style={{display:"flex"}}>
      <div style={{marginTop:"5%"}}>
      <label htmlFor="date">Form</label>
      <input
        type="date"
        id="date"
        value={selectedDate}
        onChange={handleDateChange}
      />
    
    </div>
         <Box sx={{ minWidth: 120 ,marginTop:"5%"}}>
         <div>
      <label htmlFor="date">To</label>
      <input
        type="date"
        id="date"
        value={selectedDate}
        onChange={handleDateChange}
      />
    
    </div>
    </Box>
   
    
      </Box>
      <div>
      <FormControl sx={{ m: 1, width: 270,marginTop:"10%" }}>
        <InputLabel id="demo-multiple-checkbox-label">Position Type</InputLabel>
        <Select
          labelId="demo-multiple-checkbox-label"
          id="demo-multiple-checkbox"
          multiple
          value={personName}
          onChange={handlePlaceChange}
          input={<OutlinedInput label="Tag" />}
          renderValue={(selected) => selected.join(', ')}
          MenuProps={MenuProps}
        >
          {names.map((name) => (
            <MenuItem key={name} value={name}>
              <Checkbox checked={personName.indexOf(name) > -1} />
              <ListItemText primary={name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
    <div>
      <FormControl sx={{ m: 1, width: 270,marginTop:"10%" }}>
        <InputLabel id="demo-multiple-checkbox-label">Professions</InputLabel>
        <Select
          labelId="demo-multiple-checkbox-label"
          id="demo-multiple-checkbox"
          multiple
          value={personName}
          onChange={handlePlaceChange}
          input={<OutlinedInput label="Tag" />}
          renderValue={(selected) => selected.join(', ')}
          MenuProps={MenuProps}
        >
          {names.map((name) => (
            <MenuItem key={name} value={name}>
              <Checkbox checked={personName.indexOf(name) > -1} />
              <ListItemText primary={name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
    <div style={{display:"flex",marginTop:"3%"}}>
    <Switch {...label} />
    <Typography style={{color:" rgb(104, 112, 181)"}}>
      
Exclude shifts overlapping with already signed up shifts
    </Typography>
    </div>
    <Button variant="contained" style={{ backgroundColor: '#1976d0', width: '90%', color: 'white',marginLeft:"3.5%" }}>
         <SearchIcon style={{color:"white"}}/>     Search Shifts
            </Button>
      </Box>

        </TabPanel>
        <TabPanel value="2">RECENT SEARCHES</TabPanel>
      
      </TabContext>



          </Item>
        </Grid>
        <Grid item xs={12} md={12} lg={8}>
          <Item>
          <Tabs value={value} onChange={handleChange} aria-label="icon label tabs example">
      <Tab icon={<WorkIcon />} label="MY AGENDA" />
      <Tab icon={<FeedbackIcon />} label="FEEDBACK" />
    
    </Tabs>
          </Item>
        </Grid>
      </Grid>
    </Box>
  
      <Box style={{display:"flex",marginTop:"2%"}}>
      <Card sx={{width:"10%",height:"50px",marginLeft:"2%" }}>
   <EventIcon style={{marginLeft:"15%"}} />Calender
    </Card>
    <Card sx={{width:"12%",height:"50px",marginLeft:"2%" }}>
   <EventIcon style={{marginLeft:"10%"}} />Appointments
    </Card>
      </Box>
    </Box>

 
      
    </Box>






  
    
    </div>
  );
}