import React,{ useState} from 'react';
import { Routes, Route } from 'react-router-dom';
import About from './about';
import Contact from './contact';
import Home from './home';
import Register from '../src/components/register';
import Login from '../src/components/login';
import Forgot from '../src/components/forgotpassword';
import Reset from '../src/components/resetpassword';
import Dash from '../src/components/dashboard';
import Adash from '../src/components/admindash';
import Wallet from '../src/components/wallet';
import Appointment from '../src/components/appointment';
import Profile from '../src/components/profile';
import Calender  from '../src/components/calender'
const App = () => {
  const [loggedIn, setLoggedIn] = useState(false);
  const [userName, setUserName] = useState('');

  const handleLogin = async (name) => {
    setLoggedIn(true);
    setUserName(name);
  };

  const handleLogout = () => {
    setLoggedIn(false);
    setUserName('');
  };
  return (
    <div>
      <Routes>
    
      {!loggedIn ? (
        
        <Route path="/" element={<Login onLogin={handleLogin} />} />
        
      ) : (
        <Route path="/dashboard" element={<Dash onLogout={handleLogout} userName={userName} />} />
      )}
   
        <Route path="/contact" element={<Contact />} />
    
        <Route path="/register" element={<Register />} />
        <Route path="/forgotpassword" element={<Forgot />} />
        <Route path="/resetpassword" element={<Reset />} />
      
        <Route path="/admindash" element={<Adash />} />
        <Route path="/wallet" element={<Wallet/>} />
        <Route path="/appointment" element={<Appointment/>} />
        <Route path="/profile" element={<Profile/>} />
        <Route path="/calender" element={<Calender/>} />
      </Routes>
    </div>
  );
};

export default App;
