import React, { useState } from 'react';
import '../components/login.css'
import { Link } from 'react-router-dom';

import { useNavigate } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import PublicIcon from '@mui/icons-material/Public';
import { IconButton } from '@mui/material';
export const LoginForm = ({ onLogin }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch('http://localhost:3000/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });

      if (response.ok) {
        console.log('Login successful');
        const data = await response.json();
        const { name } = data; // Replace 'name' with the property name in your response
        onLogin(name);

        navigate('/dashboard');

      } else {
        const errorData = await response.json();
        console.log('Login failed:');
        // Handle login failure (e.g., display error message)
      }
    } catch (error) {
      console.error('Error occurred during login:', error);
      // Handle login error (e.g., display error message)
    }
  };

  return (
    <div style={{ display: "flex" }} className='backcolorr'>
      <Box sx={{ flexGrow: 1 }}>
        <Grid container >
          <Grid item xs={12} lg={6} className='backcolor'>
            <div>
              <p className='title'><img src='https://sajaeventsandhospitality.com/img2/logo30.png' style={{ width: "60%" }} /></p>
            </div>

            <form action="" onSubmit={handleSubmit} style={{ marginTop: "10%" }}>
              <div className='inputc'>
                <h3>LOG IN</h3>
                <div className='input-box' style={{ margin: "auto" }}>
                  <input
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                  />
                  <label>Email</label>
                </div>
                <div className='input-box' style={{ margin: "auto", marginTop: "5%" }}  >
                  <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                  />
                  <label>Password</label>
                </div>
                <div style={{ marginLeft: "56%" }}>
                  <Link to="/forgotpassword" style={{ color: "#919191 ", fontSize: "15px", marginTop: "3%" }}>Forgot Password</Link>
                </div>
                <div style={{ display: "flex", marginTop: "4%" }} >


                  <button type="submit" className="register-button"> <Link to='/register' style={{ textDecoration: "none", color: "white" }} >SignUp</Link></button>

                  <button type="submit" className="register-button" style={{ marginLeft: "5%" }}>Login</button>


                </div>
              </div>
            </form>
            <div style={{ display: "flex", marginTop: "5%" }}>
              <IconButton style={{ color: "#919191 ", fontSize: "14px", marginLeft: "15%" }}>
                <PublicIcon style={{ color: "#919191 " }} /> Saja Events
              </IconButton>
              <IconButton style={{ color: "#919191 ", fontSize: "14px", marginLeft: "1%" }}>
                <FacebookIcon style={{ color: "#919191 " }} /> Facebook
              </IconButton>
              <IconButton style={{ color: "#919191 ", fontSize: "14px", marginLeft: "2%" }}>
                <InstagramIcon style={{ color: "#919191 " }} /> Instagram
              </IconButton>
              <IconButton style={{ color: "#919191 ", fontSize: "14px", marginLeft: "3%" }}>
                <LinkedInIcon style={{ color: "#919191 " }} /> LinkedIn
              </IconButton>
            </div>
          </Grid>
          <Grid item xs={6} className='img'>

            <img src='https://solusta.onsinch.com/agencies/themeMedia/288' style={{ width: "100%", height: "653px" }} />
          </Grid>


        </Grid>
      </Box>

    </div>
  )
}
export default LoginForm;
